	module.exports = {"data":{"markdownRemark":{"html":"<p>   When the one ring was destroyed and the hopes of the foul creatures of\nSauron diminished the slaves of one of the great orc chieftains of Mirkwood\nrebelled. The slaves slew their masters and escaped into the darkness and\nwilderness of Mirkwood. Few of the human slaves ever made it safely to their\nhomes, most died of famine or were taken by the beasts of the forest. However\na strong and powerful group of half-orc slaves managed to survive and prosper.\nThere rise to power lead to an increase in raids upon human settlements from\nthe western shores of the Anduin to the Plains of Esgaroth. Eventually the\nraids were curtailed and the tribe sank back into the depths of Mirkwood, ever\npursued by both their orc and human avengers. A few days ago a wild looking\nman was found wandering around the ruins of Vinyanost. He claimed to have been\nabducted from a farmstead close to the town as a child. The now middle-aged\nman said that he had escaped from a tribe of half-orcs a fortnight ago and\noffered to lead a party of rangers into the depths of Mirkwood to free his\nfellow slaves. The rangers were amazed to find that the trees of the forest\nseemed to shift and move and that they could not effectively track in the\nwooded labyrinth that persists in this region. The expedition returned from\nthe southwestern foot of the Mountains of Mirkwood having failed to discover\nevidence of half-orc activity or their rumoured encampment.</p>\n<p>Aeglos, Sage and Scribe of Loth Lanath.</p>","frontmatter":{"title":"Grizshask, the Half-Orc Chieftain"}}},"pathContext":{"path":"/books/grizshask"}}

